import React from 'react';
import { Icon } from "office-ui-fabric-react/lib/Icon";
import { initializeIcons } from '@uifabric/icons';
import { Scrollbars } from 'react-custom-scrollbars';
import { DatePicker } from 'office-ui-fabric-react'
import styles from '../dashboardBody.module.scss';
import '../../dashboard.css';
import $ from "jquery";

//Components
import DashboardSubProjects from '../dashboardSubProjects';

initializeIcons();

export interface IDashboardAccordianItemsState {
    open: boolean;
};
export interface IDashboardAccordianItemsProp {
    project: any;
};

const iWidth: any = window.innerWidth;
class DashboardAccordianItems extends React.Component<IDashboardAccordianItemsProp, IDashboardAccordianItemsState>{

    constructor(props: IDashboardAccordianItemsProp, state: IDashboardAccordianItemsState) {
        super(props, state);
        this.state = {
            open: false,

        };
        this.togglePanel = this.togglePanel.bind(this);
    }

    public togglePanel() {
        if (this.state.open) {
            this.setState({
                open: false,
            });
        } else {
            this.setState({
                open: true,

            });
        }
    }

    private projectOverallStatus(){
        var projectData;
        var overallStatus = styles.bgSuccess;
        return overallStatus
    }

    private getProjectTitle(){
        var projectTitle = this.props.project[0][0].name;
        return projectTitle;
    } 

    private getsubProjects(){
        var subProjects = this.props.project[1];
        return subProjects;
    }

    render() {
        return (
            <div className={this.state.open ? styles.accordianItem + " " + styles.activeAccItem : styles.accordianItem}>
                {/* <div className={styles.accordianItem + " " + styles.activeAccItem}> */}
                <div className={styles.accordianHeader}>
                    <h2 className={styles.accordianWrap} onClick={() => this.togglePanel()}>
                        <Icon iconName="ChevronRightSmall" className={styles.accordianIcon} />
                        <span className={styles.accordianHeaderText}>{this.getProjectTitle()}</span>
                    </h2>
                    <div className={styles.projectStatusBadge + " " + this.projectOverallStatus()}>
                        Success
                    </div>
                </div>
                <div className={styles.accordianPanel}>
                    <Scrollbars
                        autoHeight
                        autoHeightMin={0}
                        autoHeightMax={250}
                        renderTrackHorizontal={() => <div style={{ display: "none" }}></div>}
                    >
                        <div className={styles.accordianPanelContent}>

                            {
                                this.getsubProjects().map((subProject: any) => {
                                    return(
                                        <DashboardSubProjects key={subProject} subProject={subProject}/>
                                    )
                                })
                            }

                        </div>

                    </Scrollbars>
                </div>
            </div>
        );
    }
}

export default DashboardAccordianItems;