import React from 'react';
import { Icon } from "office-ui-fabric-react/lib/Icon";
import { initializeIcons } from '@uifabric/icons';
import { Scrollbars } from 'react-custom-scrollbars';
import { DatePicker } from 'office-ui-fabric-react'
import styles from './dashboardBody.module.scss';
import '../dashboard.css';
import $ from "jquery";

//DashboardComponents
import DashboardAccordianItems from './dashboardAccordianItems';

initializeIcons();

export interface IDashboardBodyState {
    open: boolean;
};
export interface IDashboardBodyProp {
    data: string[],
    loadFlag: boolean
};
const iWidth: any = window.innerWidth;
class DashboardBody extends React.Component<IDashboardBodyProp, IDashboardBodyState>{

    constructor(props: IDashboardBodyProp, state: IDashboardBodyState) {
        super(props, state);

        var ProjectTitle = "";
    }

    private getProjects(){
        var projects = this.props.data
        return projects;
    }

    render() {
        if(this.props.loadFlag){
            return (
                <div className={styles.contentWrap}>
                    <Scrollbars
                        //autoHide
                        style={iWidth > "768" ? { height: 'calc(100vh - 165px)' } : { height: 'calc(100vh - 65px)' }}
                        renderTrackHorizontal={() => <div style={{ display: "none" }}></div>}
                    >
                        <div className={styles.innerContentWrap}>

                            <div className={styles.weekPickerWrap}>
                                <div className={styles.weekPicker}>
                                    <span className={styles.btnPrevWeek}><Icon iconName="ChevronLeftMed" /></span>
                                    <span className={styles.weekDate}>
                                        {/* <DatePicker
                                            className="customDatepicker1"
                                            placeholder="Select a date..."
                                            ariaLabel="Select a date"
                                            showMonthPickerAsOverlay={true}
                                            
                                        /> */}
                                        15/09/2020
                                    </span>
                                    <span className={styles.btnNextWeek}><Icon iconName="ChevronRightMed" /></span>
                                </div>
                            </div>


                            <div className={styles.accordianWrap}>
                                {
                                    this.getProjects().map(project => {
                                        return(
                                            <DashboardAccordianItems key={project} project={project}/>
                                        )
                                    })
                                }
                            </div>

                        </div>
                    </Scrollbars>
                </div>
            );
        } else{
            return (
                <div className={styles.contentWrap}>
                    <div className={styles.loader}></div>
                </div>
            );
        }
    }
}

export default DashboardBody;