import React from 'react';
import { Icon } from "office-ui-fabric-react/lib/Icon";
import { initializeIcons } from '@uifabric/icons';
import { Scrollbars } from 'react-custom-scrollbars';
import { DatePicker } from 'office-ui-fabric-react'
import styles from '../dashboardBody.module.scss';
import '../../dashboard.css';
import $ from "jquery";
initializeIcons();

export interface IDashboardSubProjectsState {
    open: boolean;
};
export interface IDashboardSubProjectsProp {
    subProject: any; 

};
const iWidth: any = window.innerWidth;
class DashboardSubProjects extends React.Component<IDashboardSubProjectsProp, IDashboardSubProjectsState>{

    constructor(props: IDashboardSubProjectsProp, state: IDashboardSubProjectsState) {
        super(props, state);

        //Declaring Variables
        var title= this.props.subProject;
    }

    private getSubProjectTitle(){
        var subProjectTitle = this.props.subProject[0].SubProjectName;
        return subProjectTitle;
    }

    private getRiskCount(){
        var riskCount = this.props.subProject[3].PendingRiskItems;
        return riskCount;
    }

    private getRiskStatus(){
        var status;
        var count = this.getRiskCount();
        if(count > 0){
            status = styles.textDanger;
        } else {
            status = styles.textSuccess;
        }
        return status;
    }

    private getIssueCount(){
        var issueCount = this.props.subProject[4].PendingIssuesCount;
        return issueCount;
    }

    private getIssueStatus(){
        var status;
        var count = this.getIssueCount();
        if(count > 0){
            status = styles.textDanger;
        } else {
            status = styles.textSuccess;
        }
        return status;
    }

    private getProjectHighlights(){
        var highlights;
    }

    private getRiskItems(){
        var riskItems = this.props.subProject[3].RiskDetails;     
        if(riskItems!=null){
            return riskItems;
        } else {
            return null;
        }
    }

    private getTotalBacklogItems(){
        var total = this.props.subProject[1].TotalBacklogItems;
        return total
    }

    private getClosedBacklogItems(){
        var total = this.props.subProject[1].CompletedBacklogItems;
        return total
    }

    private getClosedBugsCount(){
        var totalBugs = this.props.subProject[2].CompletedBugItems;
        return totalBugs;
    }

    private getOpenBugsCount(){
        var totalBugs = this.props.subProject[2].PendingBugItems;
        return totalBugs;
    }

    private getTotalBugsCount(){
        var totalBugs = this.props.subProject[2].TotalBugItems;
        return totalBugs;
    }

    private getDefectRate(){
        var totalBugs = this.getTotalBugsCount();
        var openBugs = this.getOpenBugsCount();
        return (openBugs/totalBugs);
    }

    
    
    render() {
        return (
            <div className={styles.subProjectWrap}>
                {/* Header StatusWise */}
                <div className={styles.subProjectHeader + " " + styles.bgDangerLight}>
                    <div className={styles.subProjectName} title={this.getSubProjectTitle()}>{this.getSubProjectTitle()}</div>
                    <div className={styles.subProjectStatusWrap}>
                        <div className={styles.subProjectStatusInner}>
                            <span className={styles.subProjectStatus + " " + styles.bgDanger}></span>
                            <span>Delivery</span>
                        </div>
                        <div className={styles.subProjectStatusInner}>
                            <span className={styles.subProjectStatus + " " + styles.bgWarning}></span>
                            <span>Timelines</span>
                        </div>
                        <div className={styles.subProjectStatusInner}>
                            <span className={styles.subProjectStatus + " " + styles.bgSuccess}></span>
                            <span>Finance</span>
                        </div>
                        <div className={styles.subProjectStatusInner}>
                            <span className={styles.subProjectStatus + " " + styles.bgSuccessDark}></span>
                            <span>Quality</span>
                        </div>
                        <div className={styles.subProjectStatusInner + " " + this.getRiskStatus()}>
                            <span className={styles.subProjectType}>Risks: </span>
                            <span>{this.getRiskCount()}</span>
                        </div>
                        <div className={styles.subProjectStatusInner + " " + this.getIssueStatus()}>
                            <span className={styles.subProjectType}>Issues: </span>
                            <span>{this.getIssueCount()}</span>
                        </div>
                    </div>
                    <div className={styles.dashboardLinkWrap}>

                        <a href="das" className={styles.linkItem}> Dashboard</a>
                    </div>
                </div>
                <div className={styles.subProjectBody}>
                    <div className={styles.subProjectBodyInner}>
                        <div className={styles.highlightPanel}>
                            <div className={styles.panelHead}>
                                Highlights
                            </div>
                            <div>
                                Lorem ipsum dolor sit amet, consectetur adipisicing elit. Amet nemo harum voluptas aliquid
                                rem possimus nostrum excepturi!Lorem ipsum dolor sit amet, consectetur adipisicing elit.
                                Amet nemo harum voluptas aliquid rem possimus nostrum excepturi!
                            </div>
                        </div>
                        <div className={styles.riskPanel}>
                            <div className={styles.panelHead}>
                                Risks
                            </div>
                            <div>
                                <ul className={styles.riskItem}>
                                    {this.getRiskItems()!=null? 
                                        this.getRiskItems().map((risk:any) =>{
                                            return(
                                                <li key={risk.RiskTitle}>{risk.RiskTitle}</li>
                                            )    
                                        }):
                                        <li>No risk items</li>
                                    }   
                                </ul>
                            </div>
                        </div>
                        <div className={styles.workitemPanel}>
                            <div className={styles.workitem}>
                                <span className={styles.font500}>Backlog items:</span>  Total: {this.getTotalBacklogItems()}, 
                                Closed: {this.getClosedBacklogItems()}
                            </div>
                            <div className={styles.workitem}>
                                <span className={styles.font500}>Bugs:</span>  Open: {this.getOpenBugsCount()},
                                 Closed: {this.getClosedBugsCount()}
                            </div>
                            <div className={styles.workitem}>
                                <span className={styles.font500}>Defect Rate:</span> {this.getDefectRate()}
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default DashboardSubProjects;