import React from 'react';
import { initializeIcons } from '@uifabric/icons';
import styles from './dashboard.module.scss';
import './dashboard.css';

//Components
import DashboardHeader from './dashboardHeader/dashboardHeader';
import DashboardBody from './dashboardBody/dashboardBody';
import DashboardFooter from './dashboardFooter/dashboardFooter';

//Config File
import { GetDashboardDetails_BASE, GetDashboardDetails_API, GetDashboardDetails_API_KEY } from '../configFile';

initializeIcons();

export interface IDashboardState {
    data: string[],
    loadFlag: boolean
};

export interface IDashboardProp {
   
};

class Dashboard extends React.Component<IDashboardProp, IDashboardState>{

    constructor(props: IDashboardProp, state: IDashboardState) {
        super(props, state);

        this.state = { data: [], loadFlag: false };
    }

    componentDidMount() {
        var url = GetDashboardDetails_API + "?code=" + GetDashboardDetails_API_KEY; //For Local Host
        //var url = GetDashboardDetails_BASE + GetDashboardDetails_API + "?code=" + GetDashboardDetails_API_KEY;
        fetch(url)
        .then(res => res.json())
        .then(json => {
          this.setState({data: json, loadFlag: true});
        })
        .catch(error => console.error('API-ERROR:' + error.toString()));
      }

    render() {
        return (
            <div className={styles.itsDashboard}>
                <DashboardHeader />
                <DashboardBody loadFlag={this.state.loadFlag} data={this.state.data}/>
                <DashboardFooter />
            </div>
        );
    }
}

export default Dashboard;